@import '~assets/styles/h3Imports';

.container {
  max-width: rem(668);
  padding: rem(30) rem(36);

  h2 {
    font-size: rem(27);
    font-weight: bold;
  }

  div[data-modal-section='buttons'] div {
    gap: rem(16);
    @media (max-width: $max-phone-size) {
      display: flex;
      flex-direction: column;
      gap: 0;

      button {
        width: 100%;
      }
    }
  }

  figure {
    align-items: center;
    margin: 0;
    @media (max-width: $max-phone-size) {
      margin-top: rem(16);
    }
  }

  @media (max-width: $max-tablet-size) {
    max-width: rem(472);
    padding: rem(32);
  }

  @media (max-width: $max-phone-size) {
    max-width: rem(343);
    padding: rem(16);

    h2 {
      padding: 0 rem(52);
      text-align: center;
    }
  }
}
